.bg-image {
    position: relative;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%
}

.mask {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-attachment: fixed
}

.hover-overlay .mask {
    opacity: 0;
    transition: all .3s ease-in-out
}

.hover-overlay .mask:hover {
    opacity: 1
}

.hover-zoom img,
.hover-zoom video {
    transition: all .3s linear
}

.hover-zoom:hover img,
.hover-zoom:hover video {
    transform: scale(1.1)
}

.card.hover-shadow,
.hover-shadow {
    box-shadow: none;
    transition: all .3s ease-in-out
}

.card.hover-shadow:hover,
.hover-shadow:hover {
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .21);
    transition: all .3s ease-in-out
}

.bg-fixed {
    background-attachment: fixed
}

.card.hover-shadow-soft,
.hover-shadow-soft {
    box-shadow: none;
    transition: all .3s ease-in-out
}

.card.hover-shadow-soft:hover,
.hover-shadow-soft:hover {
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, .07), 0 10px 10px -5px rgba(0, 0, 0, .05);
    transition: all .3s ease-in-out
}

.form-control {
    min-height: auto;
    padding-top: 4px;
    padding-bottom: 3.28px;
    transition: all .1s linear
}

.form-control:focus {
    box-shadow: none;
    transition: all .1s linear;
    border-color: #1266f1;
    box-shadow: inset 0 0 0 1px #1266f1
}

.form-control.form-control-sm {
    font-size: .775rem;
    line-height: 1.5
}

.form-control.form-control-lg {
    line-height: 2.15;
    border-radius: .25rem
}

.form-outline {
    position: relative
}

.form-outline .form-helper {
    width: 100%;
    position: absolute;
    font-size: .875em;
    color: #757575
}

.form-outline .form-helper .form-counter {
    text-align: right
}

.form-outline .trailing {
    position: absolute;
    right: 10px;
    left: auto;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none
}

.form-outline .form-icon-trailing {
    padding-right: 2rem !important
}

.form-outline .form-control {
    min-height: auto;
    padding: .33em .75em;
    border: 0;
    background: transparent;
    transition: all .2s linear
}

.form-outline .form-control~.form-label {
    position: absolute;
    top: 0;
    max-width: 90%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    left: .75rem;
    padding-top: .37rem;
    pointer-events: none;
    transform-origin: 0 0;
    transition: all .2s ease-out;
    color: rgba(0, 0, 0, .6);
    margin-bottom: 0
}

.form-outline .form-control~.form-notch {
    display: flex;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    text-align: left;
    pointer-events: none
}

.form-outline .form-control~.form-notch div {
    pointer-events: none;
    border: 1px solid #bdbdbd;
    box-sizing: border-box;
    background: transparent
}

.form-outline .form-control~.form-notch .form-notch-leading {
    left: 0;
    top: 0;
    height: 100%;
    width: .5rem;
    border-right: none;
    border-radius: .25rem 0 0 .25rem
}

.form-outline .form-control~.form-notch .form-notch-middle {
    flex: 0 0 auto;
    width: auto;
    max-width: calc(100% - 1rem);
    height: 100%;
    border-right: none;
    border-left: none
}

.form-outline .form-control~.form-notch .form-notch-trailing {
    flex-grow: 1;
    height: 100%;
    border-left: none;
    border-radius: 0 .25rem .25rem 0
}

.form-outline .form-control:not(.placeholder-active)::-moz-placeholder {
    opacity: 0
}

.form-outline .form-control:not(.placeholder-active)::placeholder {
    opacity: 0
}

.form-outline .form-control.active::-moz-placeholder,
.form-outline .form-control:focus::-moz-placeholder {
    opacity: 1
}

.form-outline .form-control.active::placeholder,
.form-outline .form-control:focus::placeholder {
    opacity: 1
}

.form-outline .form-control:focus {
    box-shadow: none !important
}

.form-outline .form-control.active~.form-label,
.form-outline .form-control:focus~.form-label {
    transform: translateY(-1rem) translateY(.1rem) scale(.8)
}

.form-outline .form-control:focus~.form-label {
    color: #1266f1
}

.form-outline .form-control.active~.form-notch .form-notch-middle,
.form-outline .form-control:focus~.form-notch .form-notch-middle {
    border-top: none;
    border-right: none;
    border-left: none;
    transition: all .2s linear
}

.form-outline .form-control:focus~.form-notch .form-notch-middle {
    border-bottom: .125rem solid;
    border-color: #1266f1
}

.form-outline .form-control.active~.form-notch .form-notch-leading,
.form-outline .form-control:focus~.form-notch .form-notch-leading {
    border-right: none;
    transition: all .2s linear
}

.form-outline .form-control:focus~.form-notch .form-notch-leading {
    border-top: .125rem solid #1266f1;
    border-bottom: .125rem solid #1266f1;
    border-left: .125rem solid #1266f1
}

.form-outline .form-control.active~.form-notch .form-notch-trailing,
.form-outline .form-control:focus~.form-notch .form-notch-trailing {
    border-left: none;
    transition: all .2s linear
}

.form-outline .form-control:focus~.form-notch .form-notch-trailing {
    border-top: .125rem solid #1266f1;
    border-bottom: .125rem solid #1266f1;
    border-right: .125rem solid #1266f1
}

.form-outline .form-control.disabled,
.form-outline .form-control:disabled,
.form-outline .form-control[readonly] {
    background-color: #e9ecef
